<template lang="html">
<doc-section id="Popover" name="Popover">

    <popover title="slot"  placement="right" trigger="click">
        <button type="button" class="btn btn-default">slot</button>
        <form slot="content">
              <input type="text" name="name" value="" />
              <br>
              <input type="text" name="name" value="" />
              <br>
              <input type="button" name="button" value=""/>
        </form>
    </popover>

    <button type="button" class="btn btn-default">
        <popover title=""  placement="right" trigger="click" content=""></popover>
    </button>

    <button type="button" class="btn btn-default">
        <popover placement="top" trigger="click" content="left top right bottom"></popover>
    </button>

    <button type="button" class="btn btn-default">
        <popover placement="top" trigger="hover" content="hello world"></popover>
    </button>

    <button type="button" class="btn btn-default">
        <popover placement="left" trigger="focus" content="hello world"></popover>
    </button>
    <button type="button" class="btn btn-default">
        <popover placement="left" trigger="focus" content="hello world" disabled></popover>
    </button>
    <popover placement="left" trigger="click" content="hello world">
        <a href="#"></a>
    </popover>

    <doc-code language="markup" title="Component Style">
        <popover title="slot"  placement="right" trigger="click">
            <button type="button" class="btn btn-default">slot</button>
            <form slot="content">
                  <input type="text" name="name" value="" />
                  <br>
                  <input type="text" name="name" value="" />
                  <br>
                  <input type="button" name="button" value=""/>
            </form>
        </popover>

        <button type="button" class="btn btn-default">
            <popover title=""  placement="right" trigger="click" content=""></popover>
        </button>

        <button type="button" class="btn btn-default">
            <popover placement="top" trigger="click" content="left top right bottom"></popover>
        </button>

        <button type="button" class="btn btn-default">
            <popover placement="top" trigger="hover" content="hello world"></popover>
        </button>

        <button type="button" class="btn btn-default">
            <popover placement="left" trigger="focus" content="hello world"></popover>
        </button>
        <button type="button" class="btn btn-default">
            <popover placement="left" trigger="focus" content="hello world" disabled></popover>
        </button>
        <popover placement="left" trigger="click" content="hello world">
            <a href="#"></a>
        </popover>
    </doc-code>
    <doc-code language="javascript" title="Javascript Code">
        var app = new Vue({
            el: 'body',
            components: {
                popover: VueAdminBootStrap.components.Popover
            }
        });
    </doc-code>
    <doc-options>
      <div>
        <p>trigger</p>
        <p><code>String</code></p>
        <p><code>click</code></p>
        <p><code>hover</code>, <code>click</code>, <code>focus</code></p>
      </div>
      <div>
        <p>title</p>
        <p><code>String</code></p>
        <p>''</p>
        <p>,</p>
      </div>
      <div>
        <p>content</p>
        <p><code>String</code></p>
        <p><code>''</code></p>
        <p>,html</p>
      </div>
      <div>
        <p>placement</p>
        <p><code>String</code></p>
        <p><code>right</code></p>
        <p>. <code>left</code>,<code>top</code>,<code>right</code>,<code>bottom</code></p>
      </div>
      <div>
        <p>class</p>
        <p><code>String</code></p>
        <p><code>''</code></p>
        <p></p>
      </div>
      <div>
        <p>disabled</p>
        <p><code>Boolean</code></p>
        <p><code>false</code></p>
        <p></p>
      </div>
    </doc-options>
    <doc-callout type="info">
        <h4>Usage</h4>
        <p>popoverspan</p>
    </doc-callout>
    </doc-section>
</template>

<script>
import docSection from '../layout/docSection'
import docCode from '../layout/docCode'
import docOptions from '../layout/docOptions'
import docCallout from '../layout/docCallout'
import Popover from 'components/Popover'
export default {
    data: function () {
        return {
            dropdownShow: true
        }
    },
    components: {
        docSection,
        docCode,
        docOptions,
        docCallout,
        Popover
    }
}
</script>



