<template lang="html">
<doc-section id="Tooltip" name="Tooltip">
    <p>
        
        <code>
            <tooltip trigger="hover" content="hello world"></tooltip>
        </code>
        
        <tooltip trigger="click" content="hello world" placement="top">
            <code></code>
        </tooltip>
        ,
        <tooltip trigger="focus" content="hello world" class="btn btn-default">
            
        </tooltip>
        <tooltip trigger="click" class="btn btn-info" placement="bottom">
            tooltip
            <span slot="content">
                Hello World
            </span>
        </tooltip>
        <tooltip trigger="click" class="btn btn-primary" tooltip-class="admin-tooltip" content="abc">
            tooltip Class
        </tooltip>
    </p>
    <doc-code language="markup" title="Component Style">
        <p>
            
            <code>
                <tooltip trigger="hover" content="hello world"></tooltip>
            </code>
            
            <tooltip trigger="click" content="hello world" placement="top">
                <code></code>
            </tooltip>
            ,
            <tooltip trigger="focus" content="hello world" class="btn btn-default">
                
            </tooltip>
            <tooltip trigger="click" class="btn btn-info" placement="bottom">
                tooltip
                <span slot="content">
                    Hello World
                </span>
            </tooltip>
            <tooltip trigger="click" class="btn btn-primary" tooltip-class="admin-tooltip" content="abc">
                tooltip Class
            </tooltip>
        </p>
    </doc-code>
    <doc-code language="javascript" title="Javascript Code">
        var app = new Vue({
            el: 'body',
            components: {
                tooltip: VueAdminBootStrap.components.Tooltip
            }
        });
    </doc-code>
    <doc-options>
      <div>
        <p>trigger</p>
        <p><code>String</code></p>
        <p><code>click</code></p>
        <p><code>hover</code>, <code>click</code>, <code>focus</code></p>
      </div>
      <div>
        <p>class</p>
        <p><code>String</code></p>
        <p><code>''</code></p>
        <p>tooltipspan, class  span</p>
      </div>
      <div>
        <p>tooltip-class</p>
        <p><code>String</code></p>
        <p><code>tooltip</code></p>
        <p><code>tooltip-class</code></p>
      </div>
      <div>
        <p>content</p>
        <p><code>String</code></p>
        <p><code>''</code></p>
        <p>tooltip,html</p>
      </div>
      <div>
        <p>placement</p>
        <p><code>String</code></p>
        <p><code>right</code></p>
        <p>. <code>left</code>,<code>top</code>,<code>right</code>,<code>bottom</code></p>
      </div>
      <div>
        <p>disabled</p>
        <p><code>Boolean</code></p>
        <p><code>false</code></p>
        <p></p>
      </div>
    </doc-options>
    </doc-section>
</template>

<script>
import docSection from '../layout/docSection'
import docCode from '../layout/docCode'
import docOptions from '../layout/docOptions'
import docCallout from '../layout/docCallout'
import Tooltip from 'components/Tooltip'
export default {
    data: function () {
        return {
            dropdownShow: true
        }
    },
    components: {
        docSection,
        docCode,
        docOptions,
        docCallout,
        Tooltip
    }
}
</script>



